.popular {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 20px;
  margin-bottom: 60px;
}






.popular h1 {
    color: rgb(14, 14, 14);
    font-size: 50px;
    font-weight: 600;
}

.popular hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: rgb(0, 0, 0);
    margin-bottom: 50px;
}

.popular-item {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}




.popular-item img {
    width: 200px;
    height: auto;
    object-fit: contain;
}

@media (max-width: 768px) {
    .popular h1 {
        margin-top: 50px;
        text-align: center;
        font-size: 35px;
    }

    .popular hr {
        margin-bottom: 20px;
    }
}

